/**=====================
     Footer CSS Start
==========================**/
.footer {
    background-color: $footer_bg_color;
    box-shadow: $footer_box_shadow;
    padding: 15px;
    position: fixed;
    bottom: 0;
    right: 0;
    width: calc(100vw - 280px);
    transition: 0.5s;
    z-index: 1;

    @include mq-max(lg) {
        width: 100%;
        right: 0;
    }

    &.footer-dark {
        background-color: $footer_dark_color;

        p {
            color: $white;
        }
    }
}

.footer-fix {
    width: calc(100% - 280px);
    position: fixed;
}
.page-wrapper {
    .page-body-wrapper{
        .sidebar-wrapper{
            &.close_icon {
                ~.page-body{
                    div{
                        .footer{
                            width: calc(100vw - 90px);
                        }
                    }
                }
            }
        }
    }
}

/**=====================
       Footer CSS Ends
  ==========================**/